"KO odebra Konfederacji mandat w stosunku do przewidywa, rekomendacja prawidowa bo KO dostaa ostatni mandat. Wpyw turystyki prawdopodobnie by znikomy, bo tylko 4k zawiadcze - x2 do 2019.",
"Rozklad zgodny z przewidywaniami, ale wskazana strategia powinna by tylko na Lewic, bo zabrako jej 4k aby przegoni Konfederacje w biegu o ostani mandat. Tu niemal nie byo turystyki - tylko 3.5k, ale to i tak wzrost do 2019 x3.",
"Rozkad zgodny z przewidywaniami, cho rekomendacja na TD moga da 1 mandat wicej dla opozycji demokratycznej. TD zabrako tylko 1k mandatw by odebra mandat Konfederacji. 7k zawiadcze to 2.5x w stosunku do 2019.",
"Zgodnie z przewidywaniami Trzecia dostaa ostatni mandat, wic rekomendacja prawidowa, cho byo on bezpieczny, bo Konfederacja daleko z tyu. Rozkad mandatw zgodny z przewidywaniami. 6k zawiadcze to x4 w stosunku do 2019.",
"KO odebrao PiS mandat w stosunku do przewidywa, przegonia PiS o ok 100 gosw, czyli rekomendacja bya prawidowa. 12k zawiadcze to 2.5x w stosunku do 2019.",
"PiS odebra Lewicy mandat, wbrew przewidywanemu scenariuszowi. Rekomendowane wsparcie dla Lewicy byo wymagane, ale nie wystarczyo, zabrako jej 2k gosw by odebra mandat PiS. 5k zawiadcze to x3 w stosunku do 2019, ale pewnie wikszo na KO, ktore z zapasem zdobyo 3 mandaty.",
"Rekomendacja prawidowa, ale odzew nie wystarczajcy. Wyglada na to e Turystyka moga pomoc, 8k zawiadcze w stosunku to x2 w stosunku do 2019. KO wzio przedostani mandat +3k przed PiS, kttre zabrao mandat TD - zabrako jej zaledwie 300 gosw do mandatu. Przewidziana liczba mandatw dla demokratw.",
"KO wzio ostatni mandat, przeganiajc o 1000 gosw Konfederacj. Osb z zawiadczeniem 5k, czyli prawie 3x tego co w 2019. Turystyka mog mie wic wpyw.",
"KO rzeczywicie walczyo o mandat ale zostao wyminite przez TD o 5000 gosw (wynik lepszy ni przewidywany). 5k zawiadcze, czyli 3.5x tego co w 2019. Konfederacja 1000 gosw za KO. Rekomendacja bez znaczenia ale nie bdna. KO wzieo i tak przewidywan liczb mandatwm ale dawalimy szanse na +1 na KO, podczas gdy realnie +1 wzieo TD a -1 Konfederacja.",
"KO wzio ostatni mandat, Trzecia Droga przedostatni o 900 gosw wczeniej. Dobra rekomendacja - rzeczywicie Trzecia Droga potrzebowaa wsparcia. Wszystkich gosw z zawiadczeniem byo 5k, a wic ponad 3x wicej ni w 2019. Przewidywalimy na podstawie lepszych sonday Lewicy +1 dla Lewicy i zagroenie -1 dla KO.",
"Ostatni mandat wzia Trzecia Droga, rekomendacja bya za. Przewidywalimy +1 dla KO i -1 dla Trzeciej.",
"Ostatni mandat zdobya KO, 14k osb zagosowao z zawiadczeniem - jest to 48% kwoty mandatu i prawie x2 w stosunku do 2019. Jednak KO ten mandat odebraa Lewicy. Nie przewidzielimy tak niskiego poparcia dla Lewicy, a wic ryzyka straty mandatu.",
